html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; -webkit-clip-path: inset(100%); clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

/* BASE STYLES/RESETS */
/* RESETS */
h1,
h2,
h3,
h4,
p,
ul, 
li {
  margin: 0;
  padding: 0;
}

img {
   width: 100%;
   display: block;
}

.wrapper {
  max-width: 1200px;
   width: 90%;
   margin: 0 auto;
}

/* FILM PAIR SECTION STYLES */
.pair {
  display: flex;
  margin: 100px 0px;
}

.pair h3 {
  font-size: 30px;
}

.pair p {
  padding-top: 10px;
}

.film-flex {
  display: flex;
  flex-direction: row-reverse;
}

.pair-info {
  margin: 15px;
}

.pair-info,
.pair-poster {
  width: 50%;
}

.date {
  font-weight: bold;
  padding-top: 5px;
}

.fa-star {
  color: #fabc3c;
}





footer{display:flex}footer section{flex:1 1 auto}footer .attributions a{text-decoration:underline}footer .team-members{display:flex}

